 *{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    font-family: -apple-system, BlinkMacSystemFont, 
    'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 
    'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #f1f5f8;
    color: #222;
}

.booklist{
    width: 90vw;
    max-width: 1170px;
    margin: 5rem auto;
    display: grid;
    gap: 2rem; 
    /* background-color: #131313; */
    

 }

@media  screen and (min-width:768px) {
    .booklist{
        grid-template-columns: repeat(3, 1fr);
        align-items: start;
    }
}
.para{
    color: white;
}
.book{
    
    background: #131313;
    border-radius: 1rem;
    padding :1rem 2rem;
}

.book h1{
   margin-top: 1rem;
   color: white;
}
.book h4{
    color: white;
}
.image{
    width: 300px;
    height: 400px;
    border-top-left-radius: 10%;
    border-bottom-right-radius: 10%;
}
.book p {
    color: white;
    margin-top: 0.5rem;
    line-height: 1.6rem;
    letter-spacing: 0.2rem;
} 

.button{
    color: white;
}